    entryTypeMode( entryType: string ) {
        if ( entryType === 'manual' ) {
            this.manualView = true;
            this.scanView = false;
        } else if ( entryType === 'scan' ) {
            this.manualView = false;
            this.scanView = true;
        } else {
            this.manualView = false;
            this.scanView = false;
        }
    }
