    getMethodForTransferMemoDtails() {
        this.selectedEntryType = $("#entryType").val();
        this.enteredMemoNumber = $("#memoNumber").val();
        this.enteredCashMemoFormat = $("#cashMemoFormat").val();
        this.enteredCashMemoNumber = $("#cashMemoNumber").val();
        this.selectedCollectionMan = $("#collectionMan").val();
        this.userDataDtoTransferMemo = new UserDataDto();
        this.memoNum = ''
        if (this.selectedEntryType == "automatic") {
            this.memoNum = this.enteredCashMemoNumber;
        } else {
            this.memoNum = this.enteredCashMemoFormat + "" + this.enteredMemoNumber;
            if (this.enteredMemoNumber == '') {

                this.memoNum = null;

            }
        }
        this.userDataDtoTransferMemo.companyId = this.userDataDtoReturnSession.companyId;
        this.userDataDtoTransferMemo.memoNumber = this.memoNum;
        this.userDataDtoTransferMemo.mode = "cmstock";
        this.userDataDtoTransferMemo.collectioMan = this.selectedCollectionMan;
        this.userDataDtoTransferMemo.updatedBy = this.userDataDtoReturnSession.userId;
    }
    transferMemoMethod(dataForTransferMemoMethod) {
        this.getMethodForTransferMemoDtails();

        this.memoService.updateMemoDetailService(this.userDataDtoTransferMemo).subscribe(
            (response) => {
                if (response) {
                    this.updateMemoDetails = response;

                    this.chkUserDtoTransferMemo = this.updateMemoDetails;
                    if (this.chkUserDtoTransferMemo.result == "exists") {
                        $("#memoAmount").val('');
                        swal({
                            title: "Alert",
                            text: "Transfer To Collection Man Stock CashMemo " + this.userDataDtoTransferMemo.memoNumber + " already transferred to collection man stock!",
                            icon: "warning",
                            closeOnClickOutside: false,
                            closeOnEsc: false,

                        }).then(() => {
                            this.getFocusMethod();
                        })
                    } else if (this.chkUserDtoTransferMemo.result == "notexists") {
                        swal({
                            title: "Warning",
                            text: "Transfer To Collection Man Stock - CashMemo " + this.userDataDtoTransferMemo.memoNumber + " does not exist!",
                            icon: "warning",
                            closeOnClickOutside: false,
                            closeOnEsc: false,

                        }).then(() => {
                            this.getFocusMethod();
                        })
                    } else {
                        $("#memoNumber").val('');
                        $("#cashMemoNumber").val('');
                        $("#lrNumber").val('');
                        $("#memoAmount").val('')
                        this.getFocusMethod();
                        this.lRCounts = +this.lRCounts + +1;
                        this.lRCountsValues = ("" + this.lRCounts);
                        // this.collectionManObject = this.selectedCollectionMan;
                        //.getRawValue());
                        //  cashMemoDtos.add(cashMemoDto);
                        dataForTransferMemoMethod.collectionMan = this.selectedCollectionMan;
                        // need to create a obj to pass it to Print like this cashMemoDtos.add(cashMemoDto); in line no 568 in old ws CollectionManStock.java 
                        console.log(this.cashMemoDtosPrint);
                        if (this.cashMemoDtosPrint == null || this.cashMemoDtosPrint == undefined ||
                            this.cashMemoDtosPrint.length == 0) {
                            this.cashMemoDtosPrint = [];
                            this.cashMemoDtosPrint.push(dataForTransferMemoMethod);
                        } else {
                            this.cashMemoDtosPrint.push(dataForTransferMemoMethod);
                        }
                        console.log(dataForTransferMemoMethod);
                        console.log(this.cashMemoDtosPrint);
                    }

                }


                //                else {
                //                    swal( {
                //                        title: "Warning",
                //                        text: "No records found for the selected city!",
                //                        icon: "warning",
                //                        closeOnClickOutside: false,
                //                        closeOnEsc: false,
                //                    } );

                //                }
            }), (error) => swal({
                title: "Server Error",
                text: "While Updating Memo Details",
                icon: "warning",
                closeOnClickOutside: false,
                closeOnEsc: false,
            }),
            () => console.log('done');

    }


