@Component( {
    selector: 'app-billing-to-srd-collection-stock',
    templateUrl: './billing-to-srd-collection-stock.component.html',
    styleUrls: ['./billing-to-srd-collection-stock.component.css']
} )
export class BillingToSrdCollectionStockComponent implements OnInit {
    closeResult: string;



    animal: string;
    name: string;
    isLoggedIn = true;
    userDataDtoReturnSession: any;

    @ViewChild( 'content' ) content: TemplateRef<any>;
    constructor( private modalService: NgbModal,
        public dialog: MatDialog,
        private router: Router

    ) {

        {


            if ( sessionStorage.length == 0 ) {
                this.isLoggedIn = false;
                //          sweet alert starts
                swal( {
                    title: "Session Expired",
                    text: "Please relogin to access the application!",
                    icon: "error",
                    closeOnClickOutside: false,
                    closeOnEsc: false,
                } ).then(() => {
                    this.logInPage();
                } )
                //            sweet alert ends
            }

            if ( this.isLoggedIn ) {
                this.userDataDtoReturnSession = JSON.parse( sessionStorage.getItem( 'SRDWeb' ) );




            }
        }
    }
    //for popup modal starts 
    open2( content ) {
        this.modalService.open( content, { centered: true } ).result.then(
            result => {
                this.closeResult = `Closed with: ${result}`;
            },
            reason => {
                this.closeResult = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }
    //for popup modal ends 


    openDialog(): void {
        const dialogRef = this.dialog.open( BillingToSrdCollectionStockPopupComponent, {

            data: { name: this.name, animal: this.animal }
        } );

        dialogRef.afterClosed().subscribe( result => {
            //            console.log( 'The dialog was closed' );
            this.animal = result;
        } );
    }

    ngOnInit() {
        //        this.modalService.open( this.content );
        setTimeout(() => {
            this.openDialog();
        }, 0 );
    }
    logInPage() {
        this.router.navigate( ['/authentication/login'] );
    }
}

